import { HttpClient } from '@angular/common/http';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit } from '@angular/core';

import { Photo } from '@playground/domain';
import { InfiniteScrollComponent } from '@playground/shared';

@Component({
  selector: 'playground-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'full-size d-flex flex-column'
  }
})
export class GalleryComponent extends InfiniteScrollComponent<Photo> implements OnInit {

  groupedItems: Array<Photo[]> = [];

  readonly rowGap = 16;
  readonly imgSize = 150;
  readonly itemSize = this.imgSize + this.rowGap;

  override pageSize = 32;
  override loadMoreMargin = this.itemSize * 2;
  override resourceUrl = 'https://jsonplaceholder.typicode.com/photos';

  private groupSize = 8;

  constructor(
    http: HttpClient,
    cdr: ChangeDetectorRef,
    private readonly elementRef: ElementRef<HTMLElement>,
  ) {
    super(http, cdr);
  }

  override ngOnInit(): void {
    const maxRowsPerPage = Math.floor(this.elementRef.nativeElement.offsetHeight / this.itemSize);

    this.groupSize = this.calcGroupSize();
    this.pageSize = this.groupSize * (maxRowsPerPage * 2);

    super.ngOnInit();
  }

  protected override onAfterItemsAdded(photos: Photo[]): void {
    let currentGroup: Photo[] = this.groupedItems[this.groupedItems.length - 1] || [];

    if (!this.groupedItems.length) {
      this.groupedItems.push(currentGroup);
    } else if (currentGroup.length === this.groupSize) {
      currentGroup = [];
      this.groupedItems.push(currentGroup);
    }

    photos.forEach(photo => {
      currentGroup.push(photo);

      if (currentGroup.length === this.groupSize) {
        currentGroup = [];
        this.groupedItems.push(currentGroup);
      }
    });

    this.groupedItems = [...this.groupedItems];
  }

  private calcGroupSize(): number {
    const availableWidth = this.elementRef.nativeElement.offsetWidth - this.rowGap;

    return Math.floor(availableWidth / (this.itemSize));
  }

}
